@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
    @apply px-2;
}

.bg-primary {
    background: #1B262C;
}

.bg-secondary {
    background: #3282B8;
}

.bg-third {
    background: #0F4C75;
}

.mobile-nav {
    @apply absolute top-10 left-0 bg-gray-800 w-full ;
}

.mobile-nav li {
    @apply  w-full h-full text-center border-b-2;
}

.project-desc {
    @apply absolute left-0 right-0 bottom-0 top-0 bg-third opacity-0 duration-500 hover:opacity-100
}

.btn {
    @apply bg-secondary px-5 py-2 font-bold hover:border-2 border-white rounded;
}

html {
    scroll-behavior: smooth;
}
